import React from "react";
import { NavLink } from "react-router-dom";

const setActive = ({ isActive }) => (isActive ? "active-link" : "");

const Navbar = () => {
  return (
    <nav className="nav">
      <div>
        <NavLink
          to="/men"
          className={setActive}
          style={{ textDecoration: "none" }}
        >
          <div className="f">Men</div>
          {/* <img
            src="https://cdn.pixabay.com/photo/2013/04/01/21/28/man-99040__340.png"
            alt=""
          /> */}
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/women"
          className={setActive}
          style={{ textDecoration: "none" }}
        >
          <div className="f">Women</div>
          {/* <img
            src="https://cdn.pixabay.com/photo/2013/04/01/21/28/woman-99046_960_720.png"
            alt=""
          /> */}
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/bag"
          className={setActive}
          style={{ textDecoration: "none" }}
        >
          <div className="f">Bag</div>
          {/* <img
            src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSAAxy89fiA48-kT1yO-r6epMDqLGACHrZxBV6rePxfhKSpNmoXYQnnzJoGeioqiHC-SCc&usqp=CAU"
            alt=""
          /> */}
        </NavLink>
      </div>
    </nav>
  );
};

export default Navbar;
